import { Component } from '@angular/core';
import { AlertController,IonicPage, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import {   FormBuilder,    Validators } from '@angular/forms';


@IonicPage()
@Component({
  selector: 'page-parqueolicencia-ent',
  templateUrl: 'parqueolicencia-ent.html',
})
export class ParqueolicenciaEntPage {

  public loading: boolean = true;
  items: any=[];  
  items2: any=[];

  sidist:boolean=false;
  public hasFilter: boolean = false;
  tiposus: any=[];
  public form   
  public fechaini		     = (new Date()).toISOString();
  public fechafin		    = (new Date()).toISOString();
  idsuscriptor: any;
  username: string = "";
  nidsuscriptor: any;
  cui:any;
  nombre:any;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';

  constructor( private _FB          : FormBuilder,public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    public navParams: NavParams,
     public regService: Auth) {

      this.form = this._FB.group({
        fechaini     : ['', Validators.required],
        fechafin     : ['', Validators.required],
        cui : [''],
        nombre : ['']
        });

  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  




  manageGallery() : void
  {
    let self = this;
    self.loading = true;
      this.regService.getReg(  this.fechaini.substring(0, 10)+ "." + this.fechafin.substring(0, 10) +"."+ this.nombre + "/" + this.cui + "/filtro" ,'Entradascasas').then((data) => {
 
      this.items=data;
      self.loading = false;
  }, (err) => {
    self.loading = false;
    this.msj.showError3('Error',err.error);
  });


  }
    
  pideregistros() { 
      let self = this;
      self.loading = true;

      
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.idsuscriptor=this.tiposus[3]

  if(this.tiposus[0]=='Distribuidor')
  {
    this.sidist=true;
  }
  else
  {
    this.sidist=false;
  }

  this.sidist=true;
  this.regService.getReg( 'todos/todos/todos','Entradascasas').then((data) => {
 
    this.items=data;
    self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);
});
});


this.sidist=true; 
  }
  ionViewDidLoad() {
    
//this.pideregistros()

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 

  

  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  

  
}



